@import "../../node_modules/animate.css/source/_base.css";
@import "../../node_modules/animate.css/source/sliding_entrances/slideInLeft.css";
@import "../../node_modules/animate.css/source/sliding_entrances/slideInRight.css";
@import "../../node_modules/animate.css/source/sliding_entrances/slideInUp.css";

/* Typography */
$default-font-family: "Open Sans", Helvetica, "Helvetica Neue", sans-serif;
$default-font-size: 18px;
$default-font-weight: 400;

/* Colors */
$black: #2c2c2c;
$white: #ffffff;
$grey: #f6f6f6;
$grey-light: #ebebeb;
$grey-dark: #d6d4d4;

* {
    box-sizing: border-box;
}

body {
    font-family: $default-font-family;
    font-weight: $default-font-weight;
    font-size: $default-font-size;
    background-color: $grey;
    color: $black;
}

h1,
h2,
h3,
h4,
h5 {
    margin-bottom: 2rem;
}

h1 {
    font-size: 2.8rem;
    font-weight: 600;
    line-height: 3rem;
}

h1 span {
    color: $grey-light;
    font-weight: 300;
}

h2 {
    font-size: 1.8rem;
    font-weight: 400;
    line-height: 2rem;
}

h3 {
    font-size: 1.6rem;
    font-weight: 300;
    line-height: 1.8rem;
}

ul {
    margin-left: 2.6rem;
    list-style: none;
}

li::before {
    content: "‚óè";
    color: $grey-dark;
    display: inline-block;
    width: 2rem;
    margin-left: -2rem;
}

p,
ul {
    font-weight: 300;
    line-height: 1.6rem;
    margin-bottom: 2rem;
}

strong {
    font-weight: 600;
}

a.button {
    text-decoration: none;
}

a {
    color: $black;
    text-decoration: none;
    border-bottom: 1px solid $black;
}

a:hover {
    color: $black;
    border-bottom: 0;
}

.spacer {
    margin: 3rem 0;
    border-bottom: 3px solid $grey-light;
    width: 14vw;
}

.spacer.no-line {
    border-bottom: 3px solid $white;
}

.container {
    display: grid;
    grid-template-columns: auto auto;
    background-color: $white;
    margin-top: 80px;
    margin-left: 80px;
    min-height: calc(100vh - 80px);
    border-radius: 40px 0 0 0;
    box-shadow: $grey-dark -4px 0 30px;
}

.item {
    padding: 80px 20px 20px 60px;
    width: calc(50vw - 60px);
}

.char {
    width: 300px;
}

.button {
    cursor: pointer;
    background: none;
    border-radius: 40px;
    padding: 0.7rem 2.2rem;
    font-size: 1.1rem;
    font-weight: 300;
}

.center {
    text-align: center;
}

.button.black {
    color: $white;
    background-color: $black;
    border: 3px solid $black;
}

.button.black:hover {
    color: $black;
    background-color: $white;
}

.sticky {
    position: sticky;
    top: 40px;
}

.sticky:before,
.sticky:after {
    content: "";
    display: table;
}

footer {
    grid-area: 2 / 1 / 3 / 3;
    padding: 80px 20px 20px 60px;
    width: 100%;
    background-color: $black;
    color: $grey-dark;
    font-weight: 300;
    font-size: 16px;
    text-align: center;
}

footer a {
    color: $grey-dark;
    text-decoration: none;
    border-bottom: 1px solid $grey-dark;
}

footer a:hover {
    color: $grey-dark;
    border-bottom: 0;
}

footer h3 {
    margin-bottom: 1.2rem;
}

@media (max-width: 480px), (max-width: 768px) {
    h1 {
        font-size: 1.7rem;
        line-height: 2.1rem;
    }

    h2 {
        font-size: 1.3rem;
        line-height: 1.7rem;
    }

    h3 {
        font-size: 1.1rem;
        line-height: 1.3rem;
    }

    p,
    ul {
        font-size: 1rem;
        line-height: 1.4rem;
    }

    .spacer {
        margin: 2rem 0;
        width: 24vw;
    }

    .hidden-mobile {
        display: none;
    }

    .button {
        font-size: 0.9rem;
    }

    .container {
        margin: 10px 0 0 10px;
        min-height: calc(100vh - 10px);
        border-radius: 20px 0 0 0;
        box-shadow: $grey-dark -4px 0 30px;
    }

    footer,
    .item {
        padding: 30px 10px 10px 20px;
        width: 100%;
    }
}

@media (min-width: 1440px) {
    h1 {
        font-size: 3rem;
        line-height: 3.2rem;
    }

    h2 {
        font-size: 2rem;
        line-height: 2.2rem;
    }

    h3 {
        font-size: 1.8rem;
        line-height: 2rem;
    }

    p,
    ul {
        font-size: 1.2rem;
        line-height: 1.6rem;
    }

    .item {
        padding: 90px 30px 30px 70px;
        width: calc(50vw - 70px);
    }

    .char {
        width: 350px;
    }

    footer {
        padding: 90px 30px 30px 70px;
        width: 100%;
    }
}

@media (min-width: 1600px) {
    h1 {
        font-size: 3.2rem;
        line-height: 3.4rem;
    }

    h2 {
        font-size: 2.2rem;
        line-height: 2.4rem;
    }

    h3 {
        font-size: 2rem;
        line-height: 2.2rem;
    }

    p,
    ul {
        font-size: 1.4rem;
        line-height: 1.8rem;
    }

    .item {
        padding: 100px 40px 40px 80px;
        width: calc(50vw - 80px);
    }

    .char {
        width: 400px;
    }

    footer {
        padding: 100px 40px 40px 80px;
        width: 100%;
    }
}

@media (min-width: 1920px) {
    h1,
    h2,
    h3,
    h4,
    h5 {
        margin-bottom: 3rem;
    }

    h1 {
        font-size: 3.6rem;
        line-height: 3.8rem;
    }

    h2 {
        font-size: 2.6rem;
        line-height: 2.8rem;
    }

    h3 {
        font-size: 2.2rem;
        line-height: 2.4rem;
    }

    p,
    ul {
        font-size: 1.4rem;
        line-height: 1.8rem;
    }

    .spacer {
        margin: 4rem 0;
    }

    .item {
        padding: 110px 50px 50px 90px;
        width: calc(50vw - 90px);
    }

    .char {
        width: 450px;
    }

    footer {
        padding: 110px 50px 50px 90px;
        width: 100%;
    }
}
